<script>
export default {
  name: 'remote',
  components: {

  },
  data() {
    return {
      clk: 1
    }
  },
  methods: {
    btnelec() {
      var temp = document.getElementById('buttonelec')
      console.log(this.clk)
      if (this.clk){
        temp.style.animation = 'ease-in'
        temp.style.transform = 'translateY(20px)'
        document.getElementsByClassName('dng')[0].style.display = "none"
        document.getElementsByClassName('sav')[0].style.display = "block"
        document.getElementById('danger').click()

      }
      else{
        temp.style.animation = 'ease-in'
        temp.style.transform = 'translateY(0px)'
        document.getElementsByClassName('sav')[0].style.display = "none"
        document.getElementsByClassName('dng')[0].style.display = "block"
        document.getElementById('save').click()
      }
      this.clk = !this.clk
    }
  }
}
</script>

